  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: "Roboto";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

body {
  display: flex;
  flex-direction: column;
  font-family: "Roboto";
  font-size: 16px;
  max-width: 100vw;
  line-height: 1.5rem;
  align-items: center;
  background-color: #f4deb3;
}

button {
  cursor: pointer;
}

h1,
h2,
h3,
p {
  color: rgba(0, 0, 0, 0.8);
}

/* header */
header {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  background-color: #ff8a8a;
  width: 100vw;
  align-items: center;
  box-shadow: 1px 1px 2px gray;
}

#titleText {
  font-size: 3rem;
  font-weight: bolder;
  text-shadow: 3px 3px gray;
  color: black;
  padding: 20px;
  border-bottom: 6px solid black;
}

nav {
  padding: 10px;
  display: flex;
  gap: 30px;
}

nav button {
  padding: 16px 40px;
  border: none;
  font-size: 1.2rem;
  border-radius: 10px;
  font-weight: 600;
  background-color: #04bfbf8f;
  color: rgba(255, 255, 255, 0.9);
  transition: background-color 0.15s ease;
}

.activeState {
  background-color: #00c2c2;
  color: rgba(255, 255, 255, 0.9);
  box-shadow: 5px 5px 7px rgba(128, 128, 128, 0.8);
}

nav button:hover {
  background-color: #00c2c2a6;
  color: white;
  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.5);
}

/* content-div */

#content {
  flex: 1;
  padding: 50px;
  margin: 0 200px;
}

/* forms */

#todoForm,
#editTodoForm {
  display: flex;
  flex-direction: column;
  padding: 10px;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-radius: 5px;
  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.2);
}

.createTodoContainerDiv,
.editTodoContainerDiv {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.createTodoDiv,
.editTodoDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.editTodoDiv {
  width: clamp(400px, 600px, 800px);
}

.createTodoDiv input,
.createTodoDiv textarea {
  padding: 10px;
  width: clamp(300px, 500px, 700px);
  border: none;
  background-color: rgba(128, 128, 128, 0.2);
  border-radius: 5px;
}

.createTodoDiv select {
  background-color: rgba(128, 128, 128, 0.2);
  width: clamp(300px, 500px, 700px);
  padding: 5px;
}

.editTodoDiv select {
  background-color: rgba(128, 128, 128, 0.2);
  width: clamp(360px, 460px, 760px);
  padding: 5px;
}

.createTodoDiv textarea,
.editTodoDiv textarea {
  height: 50px;
  resize: none;
}

.editTodoDiv textarea,
.editTodoDiv input {
  padding: 5px;
  width: clamp(360px, 460px, 760px);
  border: none;
  background-color: rgba(128, 128, 128, 0.2);
  border-radius: 5px;
}

.createTodoDiv input:focus,
.editTodoDiv input:focus,
.createTodoDiv textarea:focus,
.editTodoDiv textarea:focus {
  outline: none;
  border: 1px solid gray;
}

#submitButton,
#editSubmitButton {
  background-color: #347928;
  padding: 10px 24px;
  color: white;
  font-size: 1.2rem;
  border: none;
  border-radius: 8px;
  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);
  transition: background-color 0.15s ease;
}
#submitButton:hover,
#editSubmitButton:hover {
  background-color: #438348;
}

/* home data */
#homeProjectsDiv {
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  align-items: center;
  justify-content: center;
}

.homeProjectDiv {
  width: clamp(400px, 600px, 800px);
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-radius: 5px;
  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.5);
  background-color: #f0eaac;
}

.homeProjectHeaderDiv {
  display: flex;
  justify-content: flex-end;
  gap: 50px;
}
.homeProjectHeaderDiv h1 {
  font-size: 1.3rem;
}

.homeProjectHeaderDiv button {
  padding: 10px 20px;
  color: white;
  font-size: 1.2rem;
  border: none;
  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);
  transition: background-color 0.15s ease;
}

.homeProjectHeaderDiv button:nth-child(2) {
  border-radius: 12px;
  background-color: gray;
}

.homeProjectHeaderDiv button:nth-child(2):hover {
  background-color: rgba(128, 128, 128, 0.9);
}

.homeProjectHeaderDiv button:last-child {
  border-radius: 8px;
  background-color: #5ab2ff;
}

.homeProjectHeaderDiv button:last-child:hover {
  background-color: #5ab2ffde;
}

.homeTodoListDiv {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px 0;
}

.homeTodoDiv {
  display: flex;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  gap: 10px;
  justify-content: flex-end;
  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);
}

.homeTodoDiv button {
  padding: 8px 20px;
  text-wrap: nowrap;
  border: none;
  border-radius: 5px;
  box-shadow: 2px 2px 3px rgba(128, 128, 128, 0.8);
  background-color: #ed9455;
  color: white;
  transition: background-color 0.15s ease;
}

.homeTodoDiv button:hover {
  background-color: #ed9455d0;
}

.homeTodoDiv h2 {
  font-size: 1.2rem;
  font-weight: 500;
}

.homeTodoDiv input[type="checkbox"],
.unchecked {
  appearance: none;
  height: 1.5rem;
  width: 1.5rem;
  border-radius: 3px;
  cursor: pointer;
  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.4);
  background-color: rgb(230, 230, 230);
  transition: background-color 0.15s ease;
}
.homeTodoDiv input[type="checkbox"]:hover {
  background-color: rgb(220, 220, 220);
}

.homeTodoDiv input[type="checkbox"]:checked::before,
.checked {
  text-align: center;
  appearance: auto;
  content: "\\2713";
  font-weight: 900;
  font-size: 1.4rem;
  height: 1.5rem;
  width: 1.5rem;
  border-radius: 3px;
  color: white;
  position: relative;
  background-color: rgb(95, 95, 95);
}

/* priority color */
.lowPriority {
  background-color: #6ac440;
}

.mediumPriority {
  background-color: #f1dc32;
}

.highPriority {
  background-color: #ff9323;
}

.veryHighPriority {
  background-color: #da0808;
}

.urgentPriority {
  background-color: #950404;
}

/* checked or completed todo */
.complete {
  background-color: rgb(170, 167, 167);
  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.2);
}

.checked,
.unchecked {
  cursor: default;
}

.complete h2,
.complete p {
  text-decoration: line-through;
  color: rgba(0, 0, 0, 0.5);
}

/* projects */
#projectsDiv {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}

.newProjectButton,
.closeProjectFormButton {
  padding: 10px 20px;
  color: white;
  background-color: #5ab2ff;
  font-size: 1.2rem;
  border: none;
  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);
  transition: background-color 0.15s ease;
  align-self: flex-end;
}
.newProjectButton {
  border-radius: 8px;
  background-color: #5ab2ff;
}

.newProjectButton:hover {
  background-color: #5ab2ffde;
}

.closeProjectFormButton {
  border-radius: 12px;
  background-color: gray;
}

.closeProjectFormButton:hover {
  background-color: rgba(128, 128, 128, 0.9);
}

#projectListDiv {
  margin-top: 30px;
  display: flex;
  gap: 40px;
}

.projectDiv {
  border: 1px solid black;
  padding: 15px 30px;
  display: flex;
  flex-wrap: nowrap;
  gap: 10px;
  align-items: center;
  background-color: #f0eaac;
  border: none;
  border-radius: 5px;
  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.5);
}

.projectDiv h1 {
  font-size: 1.2rem;
  text-wrap: nowrap;
}

.projectDiv button {
  text-wrap: nowrap;
  border: none;
  box-shadow: 2px 2px 3px rgba(128, 128, 128, 0.8);
  color: white;
  transition: background-color 0.15s ease;
}

.cancelEditTitleButton {
  margin-right: 40px;
  padding: 8px 14px;
  border-radius: 8px;
  background-color: gray;
  transition: background-color 0.15s ease;
}

.cancelEditTitleButton:hover {
  background-color: rgba(128, 128, 128, 0.9);
}

.projectDiv button:last-child {
  background-color: #55679c;
  padding: 8px 20px;
  border-radius: 5px;
}

.projectDiv button:last-child:hover {
  background-color: #55679cd7;
}

.projectDiv button:nth-last-child(2) {
  background-color: #ed9455;
  padding: 8px 20px;
  border-radius: 5px;
}

.projectDiv button:nth-last-child(2):hover {
  background-color: #ed9455d0;
}

/* project form */

#projectForm {
  display: flex;
  gap: 10px;
  width: 100%;
  justify-content: flex-end;
}

#projectForm input {
  padding: 5px;
  border: none;
  background-color: rgba(128, 128, 128, 0.2);
  border-radius: 5px;
}

#projectForm input:focus {
  outline: none;
  border: 1px solid gray;
}

#projectForm button {
  background-color: #347928;
  padding: 8px 20px;
  color: white;
  font-size: 1.2rem;
  border: none;
  border-radius: 8px;
  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);
  transition: background-color 0.15s ease;
}

#projectForm button:hover {
  background-color: #438348;
}

/* todoDetail */
.todoDetailDiv {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.backToHomeButton,
.cancelEditTodoButton {
  align-self: flex-end;
  border-radius: 12px;
  background-color: gray;
}

.backToHomeButton:hover,
.cancelEditTodoButton {
  background-color: rgba(128, 128, 128, 0.9);
}

.cancelEditTodoButton,
.backToHomeButton,
.editTodoButton,
.deleteTodoButton {
  padding: 10px 20px;
  color: white;
  font-size: 1.2rem;
  border: none;
  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);
  transition: background-color 0.15s ease;
}

.editTodoButton {
  border-radius: 8px;
  background-color: #ed9455;
}

.editTodoButton:hover {
  background-color: #ed9455d0;
}

.deleteTodoButton {
  border-radius: 8px;
  background-color: #55679c;
}

.deleteTodoButton:hover {
  background-color: #55679cd7;
}

.todoButtonContainerDiv {
  display: flex;
  gap: 10px;
}

.containerDiv {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: flex-start;
  width: clamp(400px, 600px, 800px);
}

.viewTodoDiv {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 20px;
  border-radius: 5px;
  border: none;
  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.5);
  background-color: #f0eaac;
}

.viewTodoDiv:last-child {
  align-items: flex-end;
}

.todoContainerDiv {
  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.2);
  padding: 5px;
  width: 100%;
  padding: 20px;
}

.todoContainerDiv h2 {
  font-size: 1.3rem;
  font-weight: 500;
  margin-bottom: 10px;
  text-align: left;
  text-decoration: underline;
}

.todoContainerDiv h3 {
  font-size: 1.2rem;
  font-weight: 600;
}
