n  color: white;\n  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.5);\n}\n\n/* content-div */\n\n#content {\n  flex: 1;\n  padding: 50px;\n  margin: 0 200px;\n}\n\n/* forms */\n\n#todoForm,\n#editTodoForm {\n  display: flex;\n  flex-direction: column;\n  padding: 10px;\n  align-items: center;\n  justify-content: center;\n  gap: 10px;\n  border-radius: 5px;\n  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.2);\n}\n\n.createTodoContainerDiv,\n.editTodoContainerDiv {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  align-items: center;\n}\n\n.createTodoDiv,\n.editTodoDiv {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.editTodoDiv {\n  width: clamp(400px, 600px, 800px);\n}\n\n.createTodoDiv input,\n.createTodoDiv textarea {\n  padding: 10px;\n  width: clamp(300px, 500px, 700px);\n  border: none;\n  background-color: rgba(128, 128, 128, 0.2);\n  border-radius: 5px;\n}\n\n.createTodoDiv select {\n  background-color: rgba(128, 128, 128, 0.2);\n  width: clamp(300px, 500px, 700px);\n  padding: 5px;\n}\n\n.editTodoDiv select {\n  background-color: rgba(128, 128, 128, 0.2);\n  width: clamp(360px, 460px, 760px);\n  padding: 5px;\n}\n\n.createTodoDiv textarea,\n.editTodoDiv textarea {\n  height: 50px;\n  resize: none;\n}\n\n.editTodoDiv textarea,\n.editTodoDiv input {\n  padding: 5px;\n  width: clamp(360px, 460px, 760px);\n  border: none;\n  background-color: rgba(128, 128, 128, 0.2);\n  border-radius: 5px;\n}\n\n.createTodoDiv input:focus,\n.editTodoDiv input:focus,\n.createTodoDiv textarea:focus,\n.editTodoDiv textarea:focus {\n  outline: none;\n  border: 1px solid gray;\n}\n\n#submitButton,\n#editSubmitButton {\n  background-color: #347928;\n  padding: 10px 24px;\n  color: white;\n  font-size: 1.2rem;\n  border: none;\n  border-radius: 8px;\n  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);\n  transition: background-color 0.15s ease;\n}\n#submitButton:hover,\n#editSubmitButton:hover {\n  background-color: #438348;\n}\n\n/* home data */\n#homeProjectsDiv {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 50px;\n  align-items: center;\n  justify-content: center;\n}\n\n.homeProjectDiv {\n  width: clamp(400px, 600px, 800px);\n  padding: 16px;\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  border-radius: 5px;\n  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.5);\n  background-color: #f0eaac;\n}\n\n.homeProjectHeaderDiv {\n  display: flex;\n  justify-content: flex-end;\n  gap: 50px;\n}\n.homeProjectHeaderDiv h1 {\n  font-size: 1.3rem;\n}\n\n.homeProjectHeaderDiv button {\n  padding: 10px 20px;\n  color: white;\n  font-size: 1.2rem;\n  border: none;\n  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);\n  transition: background-color 0.15s ease;\n}\n\n.homeProjectHeaderDiv button:nth-child(2) {\n  border-radius: 12px;\n  background-color: gray;\n}\n\n.homeProjectHeaderDiv button:nth-child(2):hover {\n  background-color: rgba(128, 128, 128, 0.9);\n}\n\n.homeProjectHeaderDiv button:last-child {\n  border-radius: 8px;\n  background-color: #5ab2ff;\n}\n\n.homeProjectHeaderDiv button:last-child:hover {\n  background-color: #5ab2ffde;\n}\n\n.homeTodoListDiv {\n  margin-top: 40px;\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  padding: 10px 0;\n}\n\n.homeTodoDiv {\n  display: flex;\n  align-items: center;\n  padding: 10px;\n  border-radius: 5px;\n  gap: 10px;\n  justify-content: flex-end;\n  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);\n}\n\n.homeTodoDiv button {\n  padding: 8px 20px;\n  text-wrap: nowrap;\n  border: none;\n  border-radius: 5px;\n  box-shadow: 2px 2px 3px rgba(128, 128, 128, 0.8);\n  background-color: #ed9455;\n  color: white;\n  transition: background-color 0.15s ease;\n}\n\n.homeTodoDiv button:hover {\n  background-color: #ed9455d0;\n}\n\n.homeTodoDiv h2 {\n  font-size: 1.2rem;\n  font-weight: 500;\n}\n\n.homeTodoDiv input[type=\"checkbox\"],\n.unchecked {\n  appearance: none;\n  height: 1.5rem;\n  width: 1.5rem;\n  border-radius: 3px;\n  cursor: pointer;\n  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.4);\n  background-color: rgb(230, 230, 230);\n  transition: background-color 0.15s ease;\n}\n.homeTodoDiv input[type=\"checkbox\"]:hover {\n  background-color: rgb(220, 220, 220);\n}\n\n.homeTodoDiv input[type=\"checkbox\"]:checked::before,\n.checked {\n  text-align: center;\n  appearance: auto;\n  content: \"\\2713\";\n  font-weight: 900;\n  font-size: 1.4rem;\n  height: 1.5rem;\n  width: 1.5rem;\n  border-radius: 3px;\n  color: white;\n  position: relative;\n  background-color: rgb(95, 95, 95);\n}\n\n/* priority color */\n.lowPriority {\n  background-color: #6ac440;\n}\n\n.mediumPriority {\n  background-color: #f1dc32;\n}\n\n.highPriority {\n  background-color: #ff9323;\n}\n\n.veryHighPriority {\n  background-color: #da0808;\n}\n\n.urgentPriority {\n  background-color: #950404;\n}\n\n/* checked or completed todo */\n.complete {\n  background-color: rgb(170, 167, 167);\n  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.2);\n}\n\n.checked,\n.unchecked {\n  cursor: default;\n}\n\n.complete h2,\n.complete p {\n  text-decoration: line-through;\n  color: rgba(0, 0, 0, 0.5);\n}\n\n/* projects */\n#projectsDiv {\n  display: flex;\n  flex-direction: column;\n  gap: 20px;\n  align-items: center;\n}\n\n.newProjectButton,\n.closeProjectFormButton {\n  padding: 10px 20px;\n  color: white;\n  background-color: #5ab2ff;\n  font-size: 1.2rem;\n  border: none;\n  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);\n  transition: background-color 0.15s ease;\n  align-self: flex-end;\n}\n.newProjectButton {\n  border-radius: 8px;\n  background-color: #5ab2ff;\n}\n\n.newProjectButton:hover {\n  background-color: #5ab2ffde;\n}\n\n.closeProjectFormButton {\n  border-radius: 12px;\n  background-color: gray;\n}\n\n.closeProjectFormButton:hover {\n  background-color: rgba(128, 128, 128, 0.9);\n}\n\n#projectListDiv {\n  margin-top: 30px;\n  display: flex;\n  gap: 40px;\n}\n\n.projectDiv {\n  border: 1px solid black;\n  padding: 15px 30px;\n  display: flex;\n  flex-wrap: nowrap;\n  gap: 10px;\n  align-items: center;\n  background-color: #f0eaac;\n  border: none;\n  border-radius: 5px;\n  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.5);\n}\n\n.projectDiv h1 {\n  font-size: 1.2rem;\n  text-wrap: nowrap;\n}\n\n.projectDiv button {\n  text-wrap: nowrap;\n  border: none;\n  box-shadow: 2px 2px 3px rgba(128, 128, 128, 0.8);\n  color: white;\n  transition: background-color 0.15s ease;\n}\n\n.cancelEditTitleButton {\n  margin-right: 40px;\n  padding: 8px 14px;\n  border-radius: 8px;\n  background-color: gray;\n  transition: background-color 0.15s ease;\n}\n\n.cancelEditTitleButton:hover {\n  background-color: rgba(128, 128, 128, 0.9);\n}\n\n.projectDiv button:last-child {\n  background-color: #55679c;\n  padding: 8px 20px;\n  border-radius: 5px;\n}\n\n.projectDiv button:last-child:hover {\n  background-color: #55679cd7;\n}\n\n.projectDiv button:nth-last-child(2) {\n  background-color: #ed9455;\n  padding: 8px 20px;\n  border-radius: 5px;\n}\n\n.projectDiv button:nth-last-child(2):hover {\n  background-color: #ed9455d0;\n}\n\n/* project form */\n\n#projectForm {\n  display: flex;\n  gap: 10px;\n  width: 100%;\n  justify-content: flex-end;\n}\n\n#projectForm input {\n  padding: 5px;\n  border: none;\n  background-color: rgba(128, 128, 128, 0.2);\n  border-radius: 5px;\n}\n\n#projectForm input:focus {\n  outline: none;\n  border: 1px solid gray;\n}\n\n#projectForm button {\n  background-color: #347928;\n  padding: 8px 20px;\n  color: white;\n  font-size: 1.2rem;\n  border: none;\n  border-radius: 8px;\n  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);\n  transition: background-color 0.15s ease;\n}\n\n#projectForm button:hover {\n  background-color: #438348;\n}\n\n/* todoDetail */\n.todoDetailDiv {\n  display: flex;\n  flex-direction: column;\n  gap: 50px;\n}\n\n.backToHomeButton,\n.cancelEditTodoButton {\n  align-self: flex-end;\n  border-radius: 12px;\n  background-color: gray;\n}\n\n.backToHomeButton:hover,\n.cancelEditTodoButton {\n  background-color: rgba(128, 128, 128, 0.9);\n}\n\n.cancelEditTodoButton,\n.backToHomeButton,\n.editTodoButton,\n.deleteTodoButton {\n  padding: 10px 20px;\n  color: white;\n  font-size: 1.2rem;\n  border: none;\n  box-shadow: 1px 1px 2px rgba(128, 128, 128, 0.8);\n  transition: background-color 0.15s ease;\n}\n\n.editTodoButton {\n  border-radius: 8px;\n  background-color: #ed9455;\n}\n\n.editTodoButton:hover {\n  background-color: #ed9455d0;\n}\n\n.deleteTodoButton {\n  border-radius: 8px;\n  background-color: #55679c;\n}\n\n.deleteTodoButton:hover {\n  background-color: #55679cd7;\n}\n\n.todoButtonContainerDiv {\n  display: flex;\n  gap: 10px;\n}\n\n.containerDiv {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  align-items: flex-start;\n  width: clamp(400px, 600px, 800px);\n}\n\n.viewTodoDiv {\n  display: flex;\n  flex-direction: column;\n  gap: 15px;\n  padding: 20px;\n  border-radius: 5px;\n  border: none;\n  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.5);\n  background-color: #f0eaac;\n}\n\n.viewTodoDiv:last-child {\n  align-items: flex-end;\n}\n\n.todoContainerDiv {\n  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.2);\n  padding: 5px;\n  width: 100%;\n  padding: 20px;\n}\n\n.todoContainerDiv h2 {\n  font-size: 1.3rem;\n  font-weight: 500;\n  margin-bottom: 10px;\n  text-align: left;\n  text-decoration: underline;\n}\n\n.todoContainerDiv h3 {\n  font-size: 1.2rem;\n  font-weight: 600;\n}\n"],"sourceRoot":""}]);
